@import url('https://fonts.googleapis.com/css2?family=Pattaya&display=swap');
.container-fluid{
    padding: 0%;
    margin: 0%;
    width: auto;
}
.card{
    width: 18rem;
    margin: 2rem auto;
    text-align: center;
}
.card:hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.form-outline{
    width: auto;
    margin: auto 3rem;
}
.btn{
    border: 2px solid lightgray;
}
.btn:hover{
    box-shadow: 0 4px 8px 0 rgba(85, 83, 83, 0.288), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.headlg{
    background-color: #005792;
    height: 7rem;
    margin-bottom: 1.5rem;
}
.logo{
    color: white;
    margin: auto;
    padding-top: 2rem;
    width: auto;
    text-align: center;
    font-family: 'Pattaya', sans-serif;
}
.movement{
    text-align: center;
}
.mainhead{
    width: 80%;
    padding: 3rem;
    margin: auto;
    display: flex;
    justify-content: center;
}
.srcimg{
    margin-right: 3rem;
    border-radius: 5%;
}
.desc{
    padding-top: 1.5rem;
}
.emp-profile{
    padding: 3%;
    margin-top: 3%;
    margin-bottom: 3%;
    border-radius: 0.5rem;
    background: #fff;
}
.profile-img{
    text-align: center;
}
.profile-img img{
    width: 70%;
    height: 100%;
}
.profile-img .file {
    position: relative;
    overflow: hidden;
    margin-top: -20%;
    width: 70%;
    border: none;
    border-radius: 0;
    font-size: 15px;
    background: #212529b8;
}
.profile-img .file input {
    position: absolute;
    opacity: 0;
    right: 0;
    top: 0;
}
.profile-head h5{
    color: #333;
}
.profile-head h6{
    color: #0062cc;
}
.profile-edit-btn{
    border: none;
    border-radius: 1.5rem;
    width: 70%;
    padding: 2%;
    font-weight: 600;
    color: #6c757d;
    cursor: pointer;
}
.proile-rating{
    font-size: 12px;
    color: #818182;
    margin-top: 5%;
}
.proile-rating span{
    color: #495057;
    font-size: 15px;
    font-weight: 600;
}
.profile-head .nav-tabs{
    margin-bottom:5%;
}
.profile-head .nav-tabs .nav-link{
    font-weight:600;
    border: none;
}
.profile-head .nav-tabs .nav-link.active{
    border: none;
    border-bottom:2px solid #0062cc;
}
.profile-work{
    padding: 14%;
    margin-top: -15%;
}
.profile-work p{
    font-size: 12px;
    color: #818182;
    font-weight: 600;
    margin-top: 10%;
}
.profile-work a{
    text-decoration: none;
    color: #495057;
    font-weight: 600;
    font-size: 14px;
}
.profile-work ul{
    list-style: none;
}
.profile-tab label{
    font-weight: 600;
}
.profile-tab p{
    font-weight: 600;
    color: #0062cc;
}
.hotbar{
    text-align: center;
    background-color: #005792;
    color: #fff;
    padding: 2rem;
    margin-bottom: 2rem;
}
.searchcontainer{
    display: flex;
    justify-content: center;
    height: 4rem;
}
.searchcontainer>button{
    height: inherit;
    width: 4rem;
}
.backcontainer{
    height: 6rem;
}
.line{
    height: 0.2rem;
    background-color: #005792;
}